import print from "@/utils/print.js"

let that;
export default {
	props: {
		product: {
			type: Object,
			default: {}
		} // 标题
	},
	data() {
		return {

		};
	},
	mounted() {
		that = this
	},
	methods: {
		//产品信息修改点击
		modify() {
			uni.navigateTo({
				url: '../good_add/good_add?type=more'
			});
		},

		moreActions() {
			uni.showActionSheet({
				itemList: ['产品操作记录', '关联新仓库', '删除','打印'],
				success: function(res) {
					let tapIndex = res.tapIndex + 1;
					if(tapIndex == 1){
						uni.navigateTo({
							url:'../operations/operations?objectId='+that.product.objectId+'&goodsName='+that.product.goodsName
						})
					}else if(tapIndex == 2){
						uni.navigateTo({
							url:"/pages/manage/warehouse/warehouse?type=choose"
						})
						that.$emit("isChooseStock")
					}else if(tapIndex == 3){
						that.delete_good(that.product.objectId);
					}else if(tapIndex == 4){
						print.print_goodDet(that.product);
					}
				},
			})
		},
		
		//删除商品
		delete_good(objectId) {
			let uid = uni.getStorageSync("uid")
			uni.showModal({
				title: '提示',
				content: '是否删除该商品',
				success: function(res) {
					if (res.confirm) {
						uni.setStorageSync("is_add", true)
						const query = Bmob.Query('Goods');
						query.destroy(objectId).then(res => {
							const query = Bmob.Query('Goods');
							// 单词最多删除50条
							query.equalTo("header", "==", objectId);
							query.equalTo("userId", "==", uid);
							query.find().then(todos => {
								todos.destroyAll().then(res => {
									// 成功批量修改
									console.log(res, 'ok')
									uni.navigateBack({
										delta: 1
									})
		
									setTimeout(function() {
										uni.showToast({
											title: "删除成功"
										})
									}, 1000)
		
								}).catch(err => {
									console.log(err)
								});
							})
		
						}).catch(err => {
							console.log(err)
						})
					}
				}
			});
		},
	}
}
